<template>
  <div class="w-full max-w-3xl lg:max-w-7xl mx-auto py-2 px-2 md:px-8">
    <div class="flex items-center mb-10">
      <i class="fa fa-paint-brush text-4xl text-primary mr-4"></i>
      <div>
        <h1 class="text-3xl font-bold text-primary mb-1">CSS 渐变生成器</h1>
        <p class="text-neutral-500 dark:text-neutral-300 text-base">可视化生成线性/径向渐变，支持渐变文字/边框/背景，实时预览并复制 CSS 代码。</p>
      </div>
    </div>
    <div class="bg-white dark:bg-neutral-800 border-2 border-primary/20 dark:border-primary/30 rounded-2xl shadow-2xl p-4 md:p-10">
      <div class="flex flex-col md:flex-row gap-6 md:gap-10">
        <!-- 控制面板 -->
        <div class="flex-1 min-w-[320px] border border-primary/10 dark:border-primary/20 rounded-xl bg-neutral-50 dark:bg-neutral-900 p-4 md:p-8 shadow-md">
          <div class="mb-6">
            <label class="block mb-2 text-base font-semibold text-neutral-700 dark:text-neutral-200">模式</label>
            <select v-model="mode" class="w-full p-2 rounded border bg-white dark:bg-neutral-800 text-neutral-800 dark:text-neutral-100">
              <option value="background">普通渐变背景</option>
              <option value="text">渐变文字</option>
              <option value="border">渐变边框</option>
            </select>
          </div>
          <div class="mb-6">
            <label class="block mb-2 text-base font-semibold text-neutral-700 dark:text-neutral-200">渐变类型</label>
            <select v-model="type" class="w-full p-2 rounded border bg-white dark:bg-neutral-800 text-neutral-800 dark:text-neutral-100">
              <option value="linear">线性渐变</option>
              <option value="radial">径向渐变</option>
            </select>
          </div>
          <div v-if="type==='linear'" class="mb-6">
            <label class="block mb-2 text-base font-semibold text-neutral-700 dark:text-neutral-200">角度</label>
            <div class="flex items-center gap-2">
              <input type="range" min="0" max="360" v-model.number="angle" class="w-full" />
              <input type="number" min="0" max="360" v-model.number="angle" class="w-16 p-1 rounded border bg-white dark:bg-neutral-800 text-neutral-800 dark:text-neutral-100" />
              <span class="text-sm text-neutral-500">{{ angle }}°</span>
            </div>
          </div>
          <div v-if="type==='radial'" class="mb-6">
            <label class="block mb-2 text-base font-semibold text-neutral-700 dark:text-neutral-200">形状</label>
            <select v-model="radialShape" class="w-full p-2 rounded border bg-white dark:bg-neutral-800 text-neutral-800 dark:text-neutral-100">
              <option value="circle">圆形(circle)</option>
              <option value="ellipse">椭圆(ellipse)</option>
            </select>
          </div>
          <div class="mb-6">
            <label class="block mb-2 text-base font-semibold text-neutral-700 dark:text-neutral-200">颜色</label>
            <draggable v-model="colors" handle=".drag-handle" :animation="150" class="flex flex-col gap-2">
              <div v-for="(color, idx) in colors" :key="idx" class="flex items-center gap-2 group">
                <span class="drag-handle cursor-move text-neutral-400 hover:text-primary text-lg mr-1" title="拖拽排序">☰</span>
                <input type="color" v-model="color.value" class="w-8 h-8 border rounded" />
                <input
                  type="text"
                  v-model="color.value"
                  @blur="formatColor(idx)"
                  class="w-28 p-1 rounded border bg-white dark:bg-neutral-800 text-neutral-800 dark:text-neutral-100"
                  placeholder="#RRGGBB 或 rgba"
                />
                <input type="number" min="0" max="100" v-model.number="color.pos" class="w-16 p-1 rounded border bg-white dark:bg-neutral-800 text-neutral-800 dark:text-neutral-100" />%
                <div class="flex flex-col items-center ml-2">
                  <label class="text-xs text-neutral-500 dark:text-neutral-400">透明度</label>
                  <input type="range" min="0" max="1" step="0.01" v-model.number="color.alpha" class="w-16" />
                  <span class="text-xs text-neutral-400">{{ color.alpha }}</span>
                </div>
                <button v-if="colors.length > 2" @click="removeColor(idx)" class="text-red-500 hover:underline text-xs">移除</button>
                <button v-if="idx>0" @click="moveColor(idx, -1)" class="text-xs text-neutral-400 hover:text-primary" title="上移">↑</button>
                <button v-if="idx<colors.length-1" @click="moveColor(idx, 1)" class="text-xs text-neutral-400 hover:text-primary" title="下移">↓</button>
              </div>
            </draggable>
            <button @click="addColor" class="mt-2 px-3 py-1 bg-primary text-white rounded hover:bg-secondary text-sm w-fit">添加颜色</button>
          </div>
          <div class="mb-6">
            <label class="block mb-2 text-base font-semibold text-neutral-700 dark:text-neutral-200">背景图片（可选）</label>
            <input v-model="bgImage" placeholder="@例：test.jpg（图片URL，可与渐变叠加）" class="w-full p-2 rounded border bg-white dark:bg-neutral-800 text-neutral-800 dark:text-neutral-100" />
            <div v-if="bgImage" class="mt-2 flex items-center gap-2">
              <img :src="bgImage" alt="bg" class="w-12 h-12 object-cover rounded border" />
              <button @click="bgImage=''" class="text-xs text-red-500 hover:underline">移除图片</button>
            </div>
            <div v-if="bgImage" class="mt-2 flex flex-wrap gap-2">
              <label class="text-xs text-neutral-600 dark:text-neutral-300 font-semibold">图片与渐变顺序</label>
              <select v-model="bgOrder" class="p-1 rounded border bg-white dark:bg-neutral-800 text-neutral-800 dark:text-neutral-100 text-xs">
                <option value="gradient-top">渐变在上（默认）</option>
                <option value="image-top">图片在上</option>
                <option value="only-image">仅图片</option>
                <option value="only-gradient">仅渐变</option>
              </select>
            </div>
          </div>
          <div v-if="mode==='text'" class="mb-6">
            <label class="block mb-2 text-base font-semibold text-neutral-700 dark:text-neutral-200">文字内容</label>
            <input v-model="text" class="w-full p-2 rounded border bg-white dark:bg-neutral-800 text-neutral-800 dark:text-neutral-100" />
          </div>
          <div v-if="mode==='border'" class="mb-6">
            <label class="block mb-2 text-base font-semibold text-neutral-700 dark:text-neutral-200">边框圆角</label>
            <input type="range" min="0" max="48" v-model.number="radius" class="w-full" />
            <div class="text-sm text-neutral-500 mt-1">{{ radius }}px</div>
          </div>
        </div>
        <!-- 预览与代码 -->
        <div class="flex-1 min-w-[320px] flex flex-col border border-primary/10 dark:border-primary/20 rounded-xl bg-neutral-50 dark:bg-neutral-900 p-4 md:p-8 shadow-md relative">
          <button @click="resetAll" class="absolute top-4 right-4 px-2 py-1 bg-neutral-200 dark:bg-neutral-700 text-neutral-600 dark:text-neutral-200 rounded text-xs hover:bg-primary/20 transition">重置</button>
          <label class="mb-3 text-base text-neutral-600 dark:text-neutral-300 font-semibold flex items-center">
            <i class="fa fa-eye mr-2"></i> 预览
          </label>
          <div v-if="mode==='background'" class="w-full h-40 rounded-lg border mb-2 shadow cursor-pointer transition hover:shadow-lg relative"
            :style="{ background: gradientCss }"
            @click="copyCss"
            title="点击复制 CSS"
          ></div>
          <div v-else-if="mode==='text'" class="flex items-center justify-center h-40">
            <span
              class="text-4xl md:text-5xl font-bold select-all"
              :style="textStyle"
              @click="copyCss"
              title="点击复制 CSS"
            >{{ text }}</span>
          </div>
          <div v-else class="flex items-center justify-center h-40">
            <div
              class="w-96 h-20 flex items-center justify-center text-2xl font-bold select-all border-4"
              :style="borderStyle"
              @click="copyCss"
              title="点击复制 CSS"
            >渐变边框</div>
          </div>
          <div class="mb-4 flex flex-wrap gap-2 justify-center">
            <span
              v-for="(color, idx) in colors"
              :key="idx"
              class="px-2 py-1 rounded bg-neutral-100 dark:bg-neutral-700 text-xs text-neutral-700 dark:text-neutral-200 cursor-pointer border border-neutral-200 dark:border-neutral-600 hover:bg-primary/10 hover:text-primary transition"
              @click="copyColor(color.value)"
              :title="'点击复制 ' + color.value"
            >{{ color.value }}<span v-if="mode==='background'"> <span class="text-neutral-400">({{ color.pos }}%)</span></span></span>
          </div>
          <div class="mb-6 text-xs text-neutral-400 dark:text-neutral-500 select-all break-all text-center">{{ gradientCss }}</div>
          <div class="flex gap-2 mb-2 justify-center">
            <button
              v-for="modeName in codeModes"
              :key="modeName"
              @click="codeMode=modeName"
              :class="['px-3 py-1 rounded font-semibold text-xs', codeMode===modeName ? 'bg-primary text-white' : 'bg-neutral-200 dark:bg-neutral-700 text-neutral-700 dark:text-neutral-200']"
            >{{ modeName }}</button>
          </div>
          <label class="mb-2 text-base text-neutral-600 dark:text-neutral-300 font-semibold flex items-center">
            <i class="fa fa-code mr-2"></i> CSS 代码
          </label>
          <textarea
            readonly
            class="w-full h-20 p-3 border rounded bg-neutral-50 dark:bg-neutral-900 text-sm font-mono select-all resize-none mb-4"
            :value="codeOutput"
            @focus="$event.target.select()"
          ></textarea>
          <div class="flex flex-col items-center">
            <button
              @click="copyCss"
              class="px-6 py-2 bg-primary text-white rounded-full font-semibold shadow hover:bg-secondary transition-all duration-200 text-base"
            >复制 CSS 代码</button>
            <span v-if="copied" class="text-green-500 text-sm mt-2">已复制！</span>
            <span v-if="colorCopied" class="text-blue-500 text-xs mt-2">色值已复制！</span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import draggable from 'vuedraggable'
export default {
  components: { draggable },
  data() {
    return {
      mode: 'background',
      type: 'linear',
      angle: 90,
      radialShape: 'circle',
      colors: [
        { value: '#4f8cff', pos: 0, alpha: 1 },
        { value: '#ffb86c', pos: 100, alpha: 1 }
      ],
      bgImage: '',
      bgOrder: 'gradient-top',
      text: '渐变文字',
      radius: 16,
      copied: false,
      colorCopied: false,
      codeMode: 'CSS',
      codeModes: ['CSS', 'SCSS', 'Tailwind']
    };
  },
  computed: {
    gradientCss() {
      let grad = '';
      const colorStr = this.colors.map(c => this.getColorWithAlpha(c)).join(', ');
      if (this.type === 'linear') {
        grad = `linear-gradient(${this.angle}deg, ${colorStr})`;
      } else {
        grad = `radial-gradient(${this.radialShape}, ${colorStr})`;
      }
      if (!this.bgImage) return grad;
      if (this.bgOrder === 'gradient-top') return `${grad}, url('${this.bgImage}')`;
      if (this.bgOrder === 'image-top') return `url('${this.bgImage}'), ${grad}`;
      if (this.bgOrder === 'only-image') return `url('${this.bgImage}')`;
      if (this.bgOrder === 'only-gradient') return grad;
      return grad;
    },
    textStyle() {
      return {
        background: this.gradientCss,
        WebkitBackgroundClip: 'text',
        WebkitTextFillColor: 'transparent',
        backgroundClip: 'text',
        color: 'transparent',
        cursor: 'pointer'
      };
    },
    borderStyle() {
      return {
        borderRadius: this.radius + 'px',
        border: '4px solid transparent',
        background: this.gradientCss,
        backgroundClip: 'padding-box, border-box',
        WebkitBackgroundClip: 'padding-box, border-box',
        backgroundOrigin: 'border-box',
        WebkitTextFillColor: 'initial',
        color: '#222',
        cursor: 'pointer'
      };
    },
    cssCode() {
      let grad = this.gradientCss;
      if (this.mode === 'background') {
        return `background: ${grad};`;
      }
      if (this.mode === 'text') {
        return [
          `background: ${grad};`,
          `-webkit-background-clip: text;`,
          `-webkit-text-fill-color: transparent;`,
          `background-clip: text;`,
          `color: transparent;`
        ].join('\n');
      }
      // border
      return [
        `border-radius: ${this.radius}px;`,
        `border: 4px solid transparent;`,
        `background: ${grad};`,
        `background-clip: padding-box, border-box;`,
        `-webkit-background-clip: padding-box, border-box;`,
        `background-origin: border-box;`,
        `color: #222;`
      ].join('\n');
    },
    scssCode() {
      return `$gradient: ${this.gradientCss};\nbackground: $gradient;`;
    },
    tailwindCode() {
      if (this.mode !== 'background' || this.type !== 'linear' || this.colors.length !== 2) return '// Tailwind 仅支持普通两色线性渐变';
      return `bg-gradient-to-r from-[${this.getColorWithAlpha(this.colors[0], false)}] to-[${this.getColorWithAlpha(this.colors[1], false)}]`;
    },
    codeOutput() {
      if (this.codeMode === 'CSS') return this.cssCode;
      if (this.codeMode === 'SCSS') return this.scssCode;
      if (this.codeMode === 'Tailwind') return this.tailwindCode;
      return this.cssCode;
    }
  },
  methods: {
    addColor() {
      const last = this.colors[this.colors.length - 1];
      this.colors.push({ value: '#ffffff', pos: Math.min(last.pos + 10, 100), alpha: 1 });
    },
    removeColor(idx) {
      if (this.colors.length > 2) this.colors.splice(idx, 1);
    },
    moveColor(idx, dir) {
      const arr = this.colors;
      const swapIdx = idx + dir;
      if (swapIdx < 0 || swapIdx >= arr.length) return;
      [arr[idx], arr[swapIdx]] = [arr[swapIdx], arr[idx]];
    },
    copyCss() {
      navigator.clipboard.writeText(this.codeOutput).then(() => {
        this.copied = true;
        setTimeout(() => (this.copied = false), 1200);
      });
    },
    copyColor(val) {
      navigator.clipboard.writeText(val).then(() => {
        this.colorCopied = true;
        setTimeout(() => (this.colorCopied = false), 1000);
      });
    },
    formatColor(idx) {
      let val = this.colors[idx].value.trim();
      if (/^#[0-9a-f]{3}$/i.test(val)) {
        val = '#' + val[1] + val[1] + val[2] + val[2] + val[3] + val[3];
      }
      if (/^rgba?\((.+)\)$/i.test(val)) {
        let parts = val.match(/^rgba?\((.+)\)$/i)[1].split(',').map(s => s.trim());
        if (parts.length === 3) val = `rgb(${parts.join(', ')})`;
        if (parts.length === 4) val = `rgba(${parts.join(', ')})`;
      }
      this.colors[idx].value = val;
    },
    getColorWithAlpha(color, withPercent = true) {
      // color.value: #RRGGBB or rgba/...
      if (color.value.startsWith('rgba')) {
        // 用户自定义了rgba，直接用
        return withPercent ? `${color.value} ${color.pos}%` : color.value;
      }
      if (color.value.startsWith('rgb')) {
        // 用户自定义了rgb，自动转rgba
        let rgb = color.value.match(/\d+/g);
        if (rgb && rgb.length === 3) {
          return withPercent
            ? `rgba(${rgb.join(', ')}, ${color.alpha}) ${color.pos}%`
            : `rgba(${rgb.join(', ')}, ${color.alpha})`;
        }
      }
      // #RRGGBB
      const hex = color.value.replace('#', '');
      if (hex.length === 6) {
        const r = parseInt(hex.slice(0, 2), 16);
        const g = parseInt(hex.slice(2, 4), 16);
        const b = parseInt(hex.slice(4, 6), 16);
        return withPercent
          ? `rgba(${r}, ${g}, ${b}, ${color.alpha}) ${color.pos}%`
          : `rgba(${r}, ${g}, ${b}, ${color.alpha})`;
      }
      // fallback
      return withPercent ? `${color.value} ${color.pos}%` : color.value;
    },
    resetAll() {
      this.mode = 'background';
      this.type = 'linear';
      this.angle = 90;
      this.radialShape = 'circle';
      this.colors = [
        { value: '#4f8cff', pos: 0, alpha: 1 },
        { value: '#ffb86c', pos: 100, alpha: 1 }
      ];
      this.bgImage = '';
      this.bgOrder = 'gradient-top';
      this.text = '渐变文字';
      this.radius = 16;
      this.codeMode = 'CSS';
    }
  }
};
</script>
